import React from 'react';
import styles from './MySpecInven.scss';
import classNames from 'classnames/bind';
import Radio from 'components/common/Radio';
import Checkbox from 'components/common/Checkbox';
import Adsense300250 from 'components/adsense/Adsense300250';
import Adsense72890 from 'components/adsense/Adsense72890';

const cx = classNames.bind(styles);

const MySpecInven = ({myStatInsert, myStat, setParaDoping, inputClick}) => {
  const {mainStat, itemDmgUp, limitDmg, isParasite, whereDoping} = myStat;
  
  return (
    <div className={cx('myspec-inven')}>
      <div className={cx('myspec-body')}>
        <h2 className={cx('myspec-title')}></h2>
        <ul>
          <li><b></b> :  ,   </li>
          <li><b></b> :   (28+6+6=40)</li>
          <li><b></b> :  </li>
        </ul>
        <div className={cx('spec-dv')}>
          <div className={cx('mystat')}>
            <div> <input type='number' name='mainStat' value={mainStat} onChange={myStatInsert} onClick={inputClick}/></div>
            <div> <input type='number' name='itemDmgUp' value={itemDmgUp} onChange={myStatInsert} onClick={inputClick}/></div>
            <div> <input type='number' name='limitDmg' value={limitDmg} onChange={myStatInsert} onClick={inputClick}/></div>
          </div>
          <div className={cx('parasite-doping')}>
            <div className={cx('parasite')}>
              <div><Checkbox name="isParasite" onChange={setParaDoping} checked={isParasite}></Checkbox></div>
            </div>
            <div className={cx('doping')}>
              <div className={cx('whereDoping')}>
                <div><Radio name='whereDoping' value='0' onChange={setParaDoping} defaultValue={whereDoping}></Radio></div>
                <div><Radio name='whereDoping' value='2' onChange={setParaDoping} defaultValue={whereDoping}></Radio></div>
                <div><Radio name='whereDoping' value='3' onChange={setParaDoping} defaultValue={whereDoping}></Radio></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className={cx('myspec-adsense')}>
        <Adsense300250/>
        <Adsense72890/>
      </div>
    </div>
  );
};

export default MySpecInven;